@import url(https://fonts.googleapis.com/css?family=Sacramento);
#root,
body,
html {
  height: 100%;
  width: 100%;
}

#app {
  position: relative;
  background: #fff;
  min-height: 100vh;
}

.app-content {
  padding: 60px 120px;
  background-repeat: no-repeat;
  background-position: 10% 0;
}

.app-header {
  display: -webkit-flex;
  display: flex;
  background: #fff;
  box-shadow: 0 2px 8px #f0f1f2;
  padding: 0;
}

.app-header__logo-search-section {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.app-header__logo {
  display: inline-block;
  padding: 0 20px;
}

.app-header__logo img {
  width: 36px;
}

.app-header__search-input {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding-right: 10px;
}

.app-header__search-input .ant-input-group {
  border: 0;
  width: 100%;
  max-width: 460px;
}

.app-header__search-input .ant-input-group input {
  border: 1px solid #f0f1f2;
  box-shadow: 0 2px 4px rgba(29, 34, 108, 0.1);
}

.app__affix-header {
  z-index: 99;
}
.area-field {
  width: 20%;
}
.phone-field {
  width: 80%;
}

.area-field, .phone-field {
  margin-bottom: 0;
}
@media (max-width: 75em) {
  .home {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.home__listings {
  padding: 0 0 20px;
}

.home__listings-title.ant-typography {
  color: #1d226c;
  display: inline-block;
}

.home__listing {
  text-align: center;
  margin-bottom: 2em;
  margin-top: 2em;
}

.home__calendar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.greenDay:not(.-selectedStart):not(.-selectedBetween):not(.-selectedEnd):not(.-selected) {
  border: 2px solid rgba(14, 202, 45, 0.7) !important;
}

.orangeDay:not(.-selectedStart):not(.-selectedBetween):not(.-selectedEnd):not(.-selected) {
  border: 2px solid rgba(219, 145, 60, 0.7) !important;
}

.purpleDay:not(.-selectedStart):not(.-selectedBetween):not(.-selectedEnd):not(.-selected) {
  border: 2px solid rgba(156, 136, 255, 0.7) !important;
}

.yellowDay:not(.-selectedStart):not(.-selectedBetween):not(.-selectedEnd):not(.-selected) {
  border: 2px solid rgba(228, 231, 36, 0.7) !important;
}

.home__listing > button {
  font-size: 24px;
  font-weight: 600;
  height: 40px;
  margin-bottom: .5em;
}
.cancel-button {
  float: left
}

.roommate-list, .roommate-list>.ant-space-item:first-child {
  width: 100%
}
#chart {
  margin-top: 2em;
}

.react-calendar-timeline {
  color: #4c4c4c;
}

.react-calendar-timeline .rct-scroll {
  border-right: 1px solid #eee;
}

.react-calendar-timeline .rct-calendar-header {
  border: 1px solid #eee;
  border-bottom: none;
}

.react-calendar-timeline .rct-calendar-header .days {
  border-top: 1px solid #eee;
}

.react-calendar-timeline .rct-dateHeader {
  background-color: #fff;
  border-bottom: none;
  border-left: none;
  border-right: 1px solid #eee;
  color: #4c4c4c;
}

.react-calendar-timeline .rct-horizontal-lines .rct-hl-even, .react-calendar-timeline .rct-horizontal-lines .rct-hl-odd {
  border-bottom: 1px solid #eee;
}

.react-calendar-timeline .rct-sidebar .rct-sidebar-row {
  border-bottom: 1px solid #eee;
  border-left: 1px solid #eee;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.react-calendar-timeline .rct-sidebar {
  border-right: 1px solid #eee;
  box-shadow: 10px 0 10px -5px rgb(12 12 12 / 10%);
  font-weight: 700;
  text-align: center;
  z-index: 2;
}

.react-calendar-timeline .rct-horizontal-lines .rct-hl-odd, .react-calendar-timeline .rct-sidebar .rct-sidebar-row.rct-sidebar-row-odd, .react-calendar-timeline .rct-header-root {
  background-color: #fff;
  border-bottom: 1px solid #d5d5d5;
}

.react-calendar-timeline .rct-item .rct-item-content {
  width: 100%;
}

.react-calendar-timeline .rct-vertical-lines .rct-vl {
  border-left: 1px solid #eee;
}

.react-calendar-timeline .rct-vertical-lines .rct-vl.rct-vl-first {
  border-left-width: 1px;
}

.react-calendar-timeline .rct-header-root .side-header {
  border-left: 1px solid #eee;
  border-top: 1px solid #eee;
}

.react-calendar-timeline .rct-items .reservation-item {
  border-radius: 4px;
  box-shadow: rgb(0 0 0 / 25%) 1px 1px 0px;
}

.rt-marker__label {
  position: absolute;
  left: 50%;
  display: table;
  min-width: 70px;
  height: 40px;
  padding: 0 10px;
  line-height: 1.1;
  text-align: center;
  background: currentColor;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 16px;
  font-weight: bold;
  z-index: 32;
}

.rt-marker__label::before {
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -6px;
  -webkit-transform: translateX(-1px);
          transform: translateX(-1px);
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid currentColor;
  content: ' ';
}

.rt-marker__content {
  display: table-cell;
  vertical-align: middle;
  white-space: nowrap;
  color: white;
}
.suites-list {
  margin: 2em auto 3em;
}
